nset;\r\n}\r\n\r\n///////////////////// opening style //////////////////////////\r\n.opening {\r\n    overflow: hidden;\r\n    height: 100vh;\r\n    width: 100%;\r\n    position: relative;\r\n    background-color: $secondary;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    animation: fadeOut 300ms forwards 3000ms;\r\n    z-index: 20;\r\n    position: fixed;\r\n    top: 0;\r\n    // opening image animation\r\n    #opening-image {\r\n        z-index: 20;\r\n        border-radius: 50%;\r\n        animation: trans 1s forwards, bord 400ms forwards 1s;\r\n    }\r\n    @keyframes bord {\r\n        0% {\r\n            border: 0px solid $cool-v;\r\n        }\r\n        100% {\r\n            border: 10px solid $cool-v;\r\n        }\r\n    }\r\n    @keyframes trans {\r\n        0% {\r\n            transform: scale(1);\r\n        }\r\n        100% {\r\n            transform: scale(0.8);\r\n        }\r\n    }\r\n\r\n    // before animation\r\n    &::before {\r\n        content: '';\r\n        width: 50%;\r\n        height: 100vh;\r\n        position: absolute;\r\n        left: 0;\r\n        background-color: $semi-dark;\r\n        animation: beforeB 200ms forwards 1500ms, slideLeft 1s forwards 2000ms;\r\n    }\r\n    @keyframes beforeB {\r\n        0% {\r\n            border-right: 0px solid $cool-v;\r\n        }\r\n        100% {\r\n            border-right: 5px solid $cool-v;\r\n        }\r\n    }\r\n    @keyframes slideLeft {\r\n        0% {\r\n            transform: translate(0);\r\n        }\r\n        100% {\r\n            transform: translate(-100%);\r\n        }\r\n    }\r\n\r\n    // after animation\r\n    &::after {\r\n        content: '';\r\n        width: 50%;\r\n        height: 100vh;\r\n        position: absolute;\r\n        right: 0;\r\n        background-color: $semi-dark;\r\n        animation: afterB 200ms forwards 1500ms, slideRight 1s forwards 2000ms;\r\n    }\r\n    @keyframes slideRight {\r\n        0% {\r\n            transform: translate(0);\r\n        }\r\n        100% {\r\n            transform: translate(100%);\r\n        }\r\n    }\r\n    @keyframes afterB {\r\n        0% {\r\n            border-left: 0px solid $cool-v;\r\n        }\r\n        100% {\r\n            border-left: 5px solid $cool-v;\r\n        }\r\n    }\r\n}\r\n@keyframes fadeOut {\r\n    0% {\r\n        opacity: 1;\r\n        display: unset;\r\n    }\r\n    100% {\r\n        opacity: 0;\r\n        display: none;\r\n        position: absolute;\r\n        z-index: -2;\r\n    }\r\n}\r\n\r\n///////////////////// nav bar //////////////////////////\r\nnav {\r\n    background-color: $border-v;\r\n    color: white;\r\n    border-bottom: $cool-v 3px solid;\r\n    padding: 5px 0;\r\n    position: fixed;\r\n    top: 0;\r\n    width: 100%;\r\n    z-index: 10;\r\n\r\n    .nav-flexer {\r\n        .nav-brand {\r\n            // display: none;\r\n            font-size: 1.6em;\r\n            font-weight: bold;\r\n        }\r\n\r\n        .nav-list {\r\n            .nav-items {\r\n                display: flex;\r\n                justify-content: space-evenly;\r\n                align-items: flex-start;\r\n                margin-top: 1rem;\r\n                li {\r\n                    list-style: none;\r\n                    a {\r\n                        font-size: 1.2em;\r\n                        transition: color 300ms;\r\n                    }\r\n\r\n                    a:hover {\r\n                        color: $light-blue;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// /////////////////////////profile section\r\n.profile-section {\r\n    min-height: 100vh;\r\n    margin-top: 7rem;\r\n    background: linear-gradient(to right top, darken($border-v, 50%), $border-v);\r\n    position: relative;\r\n    padding-bottom: 1.5rem;\r\n\r\n    svg {\r\n        // transform: rotate(180deg);\r\n        position: absolute;\r\n        top: 0;\r\n        fill: white;\r\n        stroke: none;\r\n    }\r\n\r\n    .pic-name-pos {\r\n        text-align: center;\r\n        padding-top: 5rem;\r\n        img {\r\n            width: 200px;\r\n            height: 200px;\r\n            border-radius: 50%;\r\n            border: 5px solid lighten($cool-v, 20%);\r\n        }\r\n\r\n        #name {\r\n            font-size: 2.5em;\r\n            font-weight: bold;\r\n            color: white;\r\n        }\r\n        #position {\r\n            color: $light-blue;\r\n            font-size: 1.2em;\r\n            font-weight: bold;\r\n        }\r\n    }\r\n\r\n    .self-descrip {\r\n        max-width: 800px;\r\n        text-align: center;\r\n        padding: 0 2rem;\r\n        margin: 3rem auto;\r\n        color: white;\r\n        line-height: 1.5;\r\n    }\r\n\r\n    .hire-btn {\r\n        display: flex;\r\n        justify-content: center;\r\n        margin-top: 2rem;\r\n        gap: 2rem;\r\n\r\n        button {\r\n            width: 100px;\r\n            padding: 7px 10px;\r\n            font-size: 1em;\r\n            border-radius: 5px;\r\n            border: 1px solid $light-blue;\r\n            background: none;\r\n            color: white;\r\n            transition: background 400ms, background-color 400ms;\r\n        }\r\n        #more-in {\r\n            border: $cool-v 1px solid;\r\n        }\r\n        #more-in:hover {\r\n            background-color: $cool-v;\r\n        }\r\n        #hire:hover {\r\n            background-color: $light-blue;\r\n        }\r\n\r\n        #hire:hover {\r\n            background-color: $light-blue;\r\n        }\r\n    }\r\n}\r\n////////////////////////////////skills\r\n.skills {\r\n    min-height: 100vh;\r\n    background-color: darken($light-blue, 5%);\r\n    position: relative;\r\n\r\n    .curve-opacity {\r\n        svg {\r\n            z-index: 1;\r\n            position: absolute;\r\n            bottom: 0;\r\n            transform: rotate(180deg);\r\n            fill: $cool-v;\r\n        }\r\n    }\r\n\r\n    header {\r\n        h1 {\r\n            color: white;\r\n            font-size: 2.7em;\r\n            padding: 1rem;\r\n        }\r\n    }\r\n\r\n    .skill-grid {\r\n        display: flex;\r\n        justify-content: start;\r\n        flex-wrap: wrap;\r\n\r\n        .skill-item {\r\n            height: 200px;\r\n            width: 200px;\r\n            box-shadow: inset 5px 5px 2px gray, inset 5px 5px 2px white;\r\n            margin: auto;\r\n            margin-bottom: 2rem;\r\n            position: relative;\r\n            border-radius: 50%;\r\n\r\n            display: flex;\r\n            align-items: center;\r\n            justify-content: center;\r\n            text-align: center;\r\n            background-color: white;\r\n            p {\r\n                color: gray;\r\n            }\r\n            .progress-bar {\r\n                position: absolute;\r\n                bottom: 0;\r\n                left: 0;\r\n                right: 0;\r\n                top: 0;\r\n\r\n                svg {\r\n                    fill: none;\r\n                    stroke: $cool-v;\r\n                    stroke-width: 20;\r\n                    stroke-dasharray: 472;\r\n                }\r\n\r\n                #html-progress {\r\n                    transition: all 600ms 6s;\r\n                    stroke-dashoffset: 94;\r\n                }\r\n                #css-progress {\r\n                    transition: all 600ms 6s;\r\n                    stroke-dashoffset: 118;\r\n                }\r\n\r\n                #css-progress {\r\n                    transition: all 600ms 6s;\r\n                    stroke-dashoffset: 118;\r\n                }\r\n                #js-progress {\r\n                    transition: all 600ms 6s;\r\n                    stroke-dashoffset: 118;\r\n                }\r\n                #bootstrap-progress {\r\n                    transition: all 600ms 6s;\r\n                    stroke-dashoffset: 47;\r\n                }\r\n\r\n                #jQuery-progress {\r\n                    transition: all 600ms 6s;\r\n                    stroke-dashoffset: 24;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// ///////////////////////////self-info\r\n.self-info {\r\n    background-color: $cool-v;\r\n    min-height: 100vh;\r\n\r\n    .self-info-grid {\r\n        text-align: center;\r\n\r\n        #address {\r\n            background-color: #00bcf03b;\r\n            display: flex;\r\n            justify-content: center;\r\n            align-items: center;\r\n            p {\r\n                color: white;\r\n            }\r\n        }\r\n\r\n        #study {\r\n            background-color: rgba(255, 255, 255, 0.212);\r\n            p {\r\n                padding: 1rem 0;\r\n            }\r\n            .date {\r\n                color: white;\r\n                font-weight: bold;\r\n            }\r\n\r\n            .school {\r\n                text-transform: uppercase;\r\n            }\r\n        }\r\n\r\n        #contacts {\r\n            background-color: white;\r\n            .contact-flexer {\r\n                display: flex;\r\n                justify-content: space-evenly;\r\n                align-items: center;\r\n                padding: 10px 0;\r\n\r\n                a {\r\n                    color: $cool-v;\r\n                    transition: all 300ms;\r\n                }\r\n                a:hover {\r\n                    color: $light-blue;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n////////// Projects ///////////\r\n#projects {\r\n    min-height: 100vh;\r\n    background: linear-gradient(to right top, black, $cool-v 50%);\r\n    color: white;\r\n\r\n    .project-grid {\r\n        .project-grid-item {\r\n            position: relative;\r\n\r\n            img {\r\n                width: 100%;\r\n            }\r\n            .project-thumbnail {\r\n                filter: brightness(65%);\r\n            }\r\n\r\n            .log-name {\r\n                position: absolute;\r\n                display: flex;\r\n                align-items: center;\r\n                bottom: 20px;\r\n                left: 20px;\r\n                gap: 1rem;\r\n                z-index: 2;\r\n                p {\r\n                    font-size: 1.5em;\r\n                    font-weight: bold;\r\n                }\r\n                .project-logo {\r\n                    width: 100px;\r\n                    height: 100px;\r\n                    border-radius: 50%;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// queries\r\n@import './sizes/queries';\r\n","@media (min-width: 50em) {\r\n    // //////opening img//////////\r\n    #opening-image {\r\n        width: 300px;\r\n        height: 300px;\r\n    }\r\n\r\n    // ///////////nav bar\r\n    .nav-flexer {\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: center;\r\n\r\n        .nav-brand {\r\n            // display: unset;\r\n            flex: 2 1;\r\n        }\r\n\r\n        .nav-list {\r\n            flex: 1 2;\r\n        }\r\n    }\r\n\r\n    // ////////////profile\r\n    .profile-section {\r\n        margin-top: 4rem;\r\n    }\r\n    .pic-name-pos {\r\n        display: flex;\r\n        justify-content: start;\r\n        align-items: center;\r\n        padding-left: 10%;\r\n        padding-top: 7rem !important;\r\n        gap: 2rem;\r\n        img {\r\n            min-height: 250px;\r\n            min-width: 250px;\r\n        }\r\n\r\n        #name {\r\n            font-size: 4em !important;\r\n        }\r\n        #position {\r\n            font-size: 1.5em !important;\r\n        }\r\n    }\r\n\r\n    .hire-btn {\r\n        justify-content: center !important;\r\n        margin-left: 10rem;\r\n\r\n        button {\r\n            min-width: 150px;\r\n            height: 50px;\r\n            font-size: 1.3;\r\n        }\r\n    }\r\n\r\n    // /////  self info //////\r\n    .self-info-grid {\r\n        padding-top: 8rem;\r\n        max-width: 860px;\r\n        margin: auto;\r\n        display: grid;\r\n        grid-template-columns: repeat(3, 1fr);\r\n        grid-template-rows: repeat(3, 1fr);\r\n        gap: 1rem;\r\n\r\n        #address {\r\n            border-radius: 8px;\r\n            grid-column: 1/3;\r\n            grid-row: 1/3;\r\n            p {\r\n                font-size: 1.5em;\r\n            }\r\n        }\r\n\r\n        #study {\r\n            grid-row: 1/4;\r\n            padding: 1rem;\r\n            border-radius: 8px;\r\n        }\r\n\r\n        #contacts {\r\n            border-radius: 8px;\r\n            grid-column: 1/3;\r\n            grid-row: 3/4;\r\n            .contact-flexer {\r\n                height: 100px;\r\n                p {\r\n                    font-size: 2em;\r\n                }\r\n            }\r\n        }\r\n    }\r\n\r\n    ////////// Projects ///////////\r\n\r\n    .project-grid {\r\n        display: grid;\r\n        grid-template-columns: repeat(3, 1fr);\r\n        grid-template-rows: repeat(3, 1fr);\r\n        gap: 0.5rem;\r\n        max-width: 1140px;\r\n        min-height: 600px;\r\n        margin: auto;\r\n\r\n        .project-grid-item {\r\n            .project-thumbnail {\r\n                position: absolute;\r\n                height: 100%;\r\n                width: 100%;\r\n            }\r\n        }\r\n        .project-grid-item:nth-of-type(1) {\r\n            grid-column: 1/3;\r\n            grid-row: 1/3;\r\n        }\r\n\r\n        .project-grid-item:nth-of-type(2) {\r\n            grid-column: 3;\r\n            grid-row: 1/4;\r\n        }\r\n\r\n        .project-grid-item:nth-of-type(3) {\r\n            grid-column: 1/3;\r\n            grid-row: 3/4;\r\n        }\r\n    }\r\n}\r\n\r\n@media (max-width: 49em) {\r\n    ////////// opening image///////////////\r\n    #opening-image {\r\n        width: 150px;\r\n        height: 150px;\r\n    }\r\n\r\n    // //////profile///////\r\n    .skills {\r\n        padding-bottom: 1rem;\r\n    }\r\n\r\n    ////////// self info///////////\r\n    .self-info-grid {\r\n        padding-top: 5rem;\r\n    }\r\n\r\n    #address {\r\n        height: 150px;\r\n        p {\r\n            font-size: 1.4em;\r\n        }\r\n    }\r\n    #study {\r\n        height: 300px;\r\n    }\r\n\r\n    .date {\r\n        font-size: 1em;\r\n    }\r\n\r\n    #contacts {\r\n        .contact-flexer {\r\n            p {\r\n                font-size: 2em;\r\n            }\r\n        }\r\n    }\r\n\r\n    ////////// Projects ///////////\r\n    .project-grid-item {\r\n        // width: 100vw;\r\n        padding: 0 10px;\r\n    }\r\n}\r\n"],"sourceRoot":""}]);
